/* Laushop Custom Styles - Enhanced with Modern Animations */

/* ==================== HAMBURGER MENU ==================== */
.hamburger-menu {
    @apply p-2 hover:bg-secondary/50 rounded-md transition-colors;
}

.hamburger-icon {
    @apply h-6 w-6 transition-transform duration-300;
}

/* Mobile Sidebar Animation */
@media (max-width: 768px) {
    aside {
        box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    }
}

/* ==================== PRODUCT CARDS ==================== */
.product-card {
    @apply transition-all duration-500 ease-out transform;
    position: relative;
    overflow: hidden;
}

.product-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
    transition: left 0.5s ease;
    z-index: 1;
}

.product-card:hover::before {
    left: 100%;
}

.product-card:hover {
    @apply shadow-lg scale-105;
    transform: translateY(-8px) scale(1.05);
}

.product-card:hover .product-image {
    @apply scale-110;
    filter: brightness(1.1);
}

.product-image {
    @apply transition-all duration-500 ease-out;
}

/* ==================== BUTTON STYLES ==================== */
.btn-primary {
    @apply text-white rounded-full px-8 py-3 text-sm uppercase tracking-widest font-medium transition-all duration-300;
    position: relative;
    overflow: hidden;
    background: linear-gradient(135deg, #8b6f47 0%, #6d5a3a 100%);
    box-shadow: 0 4px 15px rgba(139, 111, 71, 0.25);
    color: #ffffff;
}

.btn-primary::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
    transition: left 0.5s ease;
}

.btn-primary:hover {
    @apply shadow-xl;
    transform: translateY(-2px);
    background: linear-gradient(135deg, #a0845f 0%, #7d6a47 100%);
}

.btn-primary:hover::before {
    left: 100%;
}

.btn-secondary {
    @apply text-white rounded-full px-8 py-3 text-sm uppercase tracking-widest font-medium transition-all duration-300;
    background: linear-gradient(135deg, #c5a572 0%, #b8956a 100%);
    box-shadow: 0 4px 15px rgba(197, 165, 114, 0.25);
}

.btn-secondary:hover {
    @apply shadow-lg;
    transform: translateY(-2px);
    background: linear-gradient(135deg, #d4b896 0%, #c9a984 100%);
}

.btn-outline {
    @apply border-2 border-[#8b6f47] text-[#8b6f47] rounded-full px-8 py-3 text-sm uppercase tracking-widest font-medium transition-all duration-300;
    position: relative;
    overflow: hidden;
}

.btn-outline::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: rgba(139, 111, 71, 0.05);
    transition: left 0.4s ease;
    z-index: -1;
}

.btn-outline:hover {
    @apply border-[#a0845f];
    transform: translateY(-2px);
    background: rgba(139, 111, 71, 0.05);
}

.btn-outline:hover::before {
    left: 0;
}

/* ==================== INPUT STYLES ==================== */
.input-minimal {
    @apply h-12 bg-transparent border-b-2 border-[#d4c5b9] focus:border-[#8b6f47] rounded-none px-0 transition-colors placeholder:text-muted-foreground/50 outline-none;
}

.input-minimal:focus {
    @apply border-[#8b6f47];
    box-shadow: 0 2px 0 rgba(139, 111, 71, 0.1);
}

/* ==================== GLASS EFFECT ==================== */
.glass {
    @apply backdrop-blur-xl bg-white/80 border-b border-white/30;
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.6));
}

/* ==================== ADMIN SIDEBAR ==================== */
.admin-sidebar {
    @apply w-64 bg-white border-r border-[#d4c5b9] min-h-screen transition-all duration-300;
    box-shadow: 2px 0 8px rgba(139, 111, 71, 0.08);
}

.admin-nav-item {
    @apply flex items-center gap-3 px-4 py-3 text-[#7d6a54] hover:text-[#8b6f47] transition-all duration-300 relative;
    overflow: hidden;
}

.admin-nav-item::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 3px;
    background: linear-gradient(180deg, #8b6f47 0%, #c5a572 100%);
    transform: scaleY(0);
    transition: transform 0.3s ease;
    transform-origin: top;
}

.admin-nav-item:hover {
    @apply bg-[#faf5f1];
}

.admin-nav-item:hover::before {
    transform: scaleY(1);
}

.admin-nav-item.active {
    @apply text-[#8b6f47] bg-[#faf5f1];
}

.admin-nav-item.active::before {
    transform: scaleY(1);
}

/* ==================== CARD STYLES ==================== */
.card-minimal {
    @apply bg-white border border-[#d4c5b9] shadow-sm p-6 rounded-lg transition-all duration-300;
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.95) 0%, rgba(255, 255, 255, 0.85) 100%);
}

.card-minimal:hover {
    @apply shadow-lg border-[#c5a572];
    transform: translateY(-4px);
}

/* ==================== HERO TEXT ANIMATION ==================== */
.hero-title {
    animation: heroFadeIn 0.8s ease-out forwards;
    letter-spacing: -0.03em;
    font-weight: 700;
    color: #1a1a1a;
}

.hero-subtitle {
    animation: heroFadeIn 0.8s ease-out forwards 0.2s both;
}

.hero-description {
    animation: heroFadeIn 0.8s ease-out forwards 0.4s both;
}

@keyframes heroFadeIn {
    from {
        opacity: 0;
        transform: translateY(30px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* ==================== STAGGERED ANIMATIONS ==================== */
.stagger-1 { animation-delay: 0.05s; }
.stagger-2 { animation-delay: 0.1s; }
.stagger-3 { animation-delay: 0.15s; }
.stagger-4 { animation-delay: 0.2s; }
.stagger-5 { animation-delay: 0.25s; }
.stagger-6 { animation-delay: 0.3s; }
.stagger-7 { animation-delay: 0.35s; }
.stagger-8 { animation-delay: 0.4s; }

/* ==================== LOADING SKELETON ==================== */
.skeleton {
    @apply animate-pulse bg-gradient-to-r from-[#d4c5b9] via-[#e8ddd0] to-[#d4c5b9] rounded-lg;
    background-size: 1000px 100%;
    animation: shimmer 2s linear infinite;
}

@keyframes shimmer {
    0% {
        background-position: -1000px 0;
    }
    100% {
        background-position: 1000px 0;
    }
}

/* ==================== BADGE STYLES ==================== */
.badge-sale {
    @apply absolute top-4 left-4 bg-gradient-to-r from-[#d4635f] to-[#c5a572] text-white text-xs uppercase tracking-wider px-3 py-1 rounded-full shadow-md;
    animation: float 3s ease-in-out infinite;
}

.badge-new {
    @apply absolute top-4 right-4 bg-gradient-to-r from-green-500 to-emerald-500 text-white text-xs uppercase tracking-wider px-3 py-1 rounded-full shadow-md;
}

/* ==================== RATING STARS ==================== */
.star-filled {
    @apply text-yellow-400 drop-shadow-sm transition-all duration-200;
}

.star-filled:hover {
    @apply scale-110;
    filter: drop-shadow(0 0 4px rgba(250, 204, 21, 0.5));
}

.star-empty {
    @apply text-gray-300 transition-colors duration-200;
}

/* ==================== MISC EFFECTS ==================== */
.pulse-ring {
    animation: pulseRing 2s ease-out infinite;
}

@keyframes pulseRing {
    0% {
        box-shadow: 0 0 0 0 rgba(15, 23, 42, 0.7);
    }
    50% {
        box-shadow: 0 0 0 10px rgba(15, 23, 42, 0);
    }
    100% {
        box-shadow: 0 0 0 0 rgba(15, 23, 42, 0);
    }
}

/* Smooth hover lift effect */
.hover-lift {
    @apply transition-all duration-300 ease-out;
}

.hover-lift:hover {
    transform: translateY(-8px);
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
}
