@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400;500;600;700&family=Lora:wght@400;500;600;700&family=Inter:wght@300;400;500;600;700&family=JetBrains+Mono:wght@400;500&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    :root {
        --background: 0 0% 100%;
        --foreground: 20 10% 15%;
        --card: 0 0% 100%;
        --card-foreground: 20 10% 15%;
        --popover: 0 0% 100%;
        --popover-foreground: 20 10% 15%;
        --primary: 0 0% 13%;
        --primary-foreground: 0 0% 100%;
        --secondary: 40 20% 94%;
        --secondary-foreground: 20 10% 15%;
        --accent: 35 40% 55%;
        --accent-foreground: 0 0% 100%;
        --muted: 0 0% 96%;
        --muted-foreground: 0 0% 50%;
        --destructive: 0 84% 60%;
        --destructive-foreground: 0 0% 98%;
        --border: 0 0% 90%;
        --input: 0 0% 90%;
        --ring: 25 35% 45%;
        --radius: 0.125rem;
        --chart-1: 25 35% 45%;
        --chart-2: 0 0% 45%;
        --chart-3: 0 0% 70%;
        --chart-4: 40 20% 94%;
        --chart-5: 0 0% 85%;
    }
}

@layer base {
    * {
        @apply border-border;
    }
    body {
        @apply bg-background text-foreground;
        font-family: 'Inter', sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        font-weight: 400;
        letter-spacing: 0.3px;
    }
    h1 {
        font-family: 'Cormorant Garamond', serif;
        font-weight: 700;
        letter-spacing: -0.03em;
        color: hsl(20 10% 15%);
    }
    h2 {
        font-family: 'Cormorant Garamond', serif;
        font-weight: 600;
        letter-spacing: -0.02em;
        color: hsl(20 10% 15%);
    }
    h3 {
        font-family: 'Lora', serif;
        font-weight: 600;
        letter-spacing: -0.01em;
        color: hsl(20 10% 15%);
    }
    h4, h5, h6 {
        font-family: 'Inter', sans-serif;
        font-weight: 600;
        color: hsl(20 10% 15%);
    }
    p {
        line-height: 1.6;
        color: hsl(0 0% 50%);
    }
    a {
        transition: color 0.3s ease;
    }
    code {
        font-family: 'JetBrains Mono', monospace;
    }
}

@layer utilities {
    .font-heading {
        font-family: 'Cormorant Garamond', serif;
        font-weight: 700;
        letter-spacing: -0.03em;
    }
    .font-subheading {
        font-family: 'Lora', serif;
        font-weight: 600;
    }
    .font-body {
        font-family: 'Inter', sans-serif;
    }
    .font-mono {
        font-family: 'JetBrains Mono', monospace;
    }
    .text-balance {
        text-wrap: balance;
    }
    /* Modern Animations */
    .animate-fade-in {
        animation: fadeIn 0.5s ease-out forwards;
    }
    .animate-slide-up {
        animation: slideUp 0.6s ease-out forwards;
    }
    .animate-slide-down {
        animation: slideDown 0.6s ease-out forwards;
    }
    .animate-slide-left {
        animation: slideLeft 0.6s ease-out forwards;
    }
    .animate-slide-right {
        animation: slideRight 0.6s ease-out forwards;
    }
    .animate-float {
        animation: float 3s ease-in-out infinite;
    }
    .animate-bounce-gentle {
        animation: bounceGentle 2s ease-in-out infinite;
    }
    .animate-glow {
        animation: glow 2s ease-in-out infinite;
    }
    .animate-pulse-soft {
        animation: pulseSoft 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
    }
    .animate-shimmer {
        animation: shimmer 2s linear infinite;
    }
    .animate-scale-in {
        animation: scaleIn 0.4s ease-out forwards;
    }
    .animate-flip {
        animation: flip 0.6s ease-out forwards;
    }
    /* Stagger animations */
    .animate-stagger-1 {
        animation-delay: 0.05s;
    }
    .animate-stagger-2 {
        animation-delay: 0.1s;
    }
    .animate-stagger-3 {
        animation-delay: 0.15s;
    }
    .animate-stagger-4 {
        animation-delay: 0.2s;
    }
    .animate-stagger-5 {
        animation-delay: 0.25s;
    }
    /* Glass effect */
    .glass-effect {
        @apply backdrop-blur-md bg-white/80 dark:bg-slate-900/80 border border-white/20 dark:border-white/10;
    }
    /* Gradient backgrounds */
    .gradient-primary {
        @apply bg-gradient-to-r from-slate-900 via-slate-800 to-slate-900;
    }
    .gradient-warm {
        @apply bg-gradient-to-r from-amber-50 via-orange-50 to-amber-50;
    }
    .gradient-text {
        @apply bg-gradient-to-r from-slate-900 to-slate-600 bg-clip-text text-transparent;
    }
    /* Shadow effects */
    .shadow-soft {
        @apply shadow-sm;
    }
    .shadow-glow {
        @apply shadow-lg;
    }
    .shadow-deep {
        box-shadow: 0 20px 40px rgba(0, 0, 0, 0.08);
    }
}

/* Advanced Keyframe Animations */
@keyframes fadeIn {
    from { 
        opacity: 0;
    }
    to { 
        opacity: 1;
    }
}

@keyframes slideUp {
    from { 
        opacity: 0;
        transform: translateY(20px);
    }
    to { 
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes slideDown {
    from { 
        opacity: 0;
        transform: translateY(-20px);
    }
    to { 
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes slideLeft {
    from { 
        opacity: 0;
        transform: translateX(20px);
    }
    to { 
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes slideRight {
    from { 
        opacity: 0;
        transform: translateX(-20px);
    }
    to { 
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes float {
    0%, 100% {
        transform: translateY(0px);
    }
    50% {
        transform: translateY(-10px);
    }
}

@keyframes bounceGentle {
    0%, 100% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-5px);
    }
}

@keyframes glow {
    0%, 100% {
        box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    }
    50% {
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
    }
}

@keyframes pulseSoft {
    0%, 100% {
        opacity: 1;
    }
    50% {
        opacity: 0.7;
    }
}

@keyframes shimmer {
    0% {
        background-position: -1000px 0;
    }
    100% {
        background-position: 1000px 0;
    }
}

@keyframes scaleIn {
    from {
        opacity: 0;
        transform: scale(0.95);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}

@keyframes flip {
    from {
        opacity: 0;
        transform: rotateY(-90deg);
    }
    to {
        opacity: 1;
        transform: rotateY(0);
    }
}

/* Custom scrollbar */
::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

::-webkit-scrollbar-track {
    background: #f0efe9;
}

::-webkit-scrollbar-thumb {
    background: #d4c4b7;
    border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
    background: #c4b4a7;
}

/* Selection */
::selection {
    background: #d4c4b7;
    color: #1a1a1a;
}

@layer base {
    [data-debug-wrapper="true"] {
        display: contents !important;
    }
}
